import React from 'react';
import PatinhasSlots from './components/PatinhasSlots';
import PromoBanner from './components/PromoBanner';
import './App.css';

function App() {
  return (
    <div className="App">
      <header className="App-header-theme">
        <h1>Caixa-Forte do Patinhas </h1>
        <p className="subtitle">Simulador educativo de volatilidade e reforo intermitente.</p>
      </header>
      <main>
        <PatinhasSlots />
      </main>
      <PromoBanner />
      <footer className="footer-theme">
        <p> Este simulador demonstra matematicamente como jogos de azar so projetados para sempre favorecer a "casa" a longo prazo.</p>
      </footer>
    </div>
  );
}

export default App;